* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.top,
.about,
.service,
.product,
.impression {
  min-height: 100vh;
}

.title-contents {
  margin-top: 100px;
  font-size: 40px;
  font-weight: bold;
  color: transparent;
  background: linear-gradient(to right, #05daff, 40%, #7025b6);
  background-clip: text;
  text-align: center;
}

.top {
  position: relative;
  background-image: url("../img/top_bg.jpg");
  background-repeat: no-repeat;
  background-position: 20%;
  background-size: cover;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 0;
}

.top-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(150deg, #05daff, #2f00ff);
  opacity: 0.6;
  z-index: -1;
}

.top::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 15vw solid #fff;
  border-left: 100vw solid transparent;
}

.top .title-primary {
  color: #fff;
  font-size: 80px;
  letter-spacing: 20px;
}

.top .title-secondary {
  color: #fff;
  font-size: 40px;
  letter-spacing: 3px;
}

.top-btn {
  padding: 15px 40px;
  font-size: 25px;
  border-radius: 25px;
  background: #fff;
  cursor: pointer;
}

.about .wrapper {
  display: flex;
  margin-top: 50px;
}

.about .wrapper .contents {
  width: 50%;
  margin: 0 20px;
}

.about .wrapper .contents-detail {
  margin-top: 30px;
}

.about .wrapper .contents-detail_title {
  font-size: 30px;
  font-weight: bold;
  color: #888;
}

.about .wrapper .contents-detail_text {
  margin-top: 10px;
  color: #666;
  font-size: 20px;
}

.about .wrapper .contents-link {
  display: block;
  margin-top: 50px;
  font-size: 20px;
  color: #05fff3;
}

.about .wrapper .images {
  position: relative;
  width: 50%;
}

.about .wrapper .images img {
  position: absolute;
  width: 55%;
  box-shadow: #888 0 0 15px 3px;
  transition: 0.5s;
}

.about .wrapper .images img:hover {
  z-index: 10;
  width: 70%;
  transition: 0.5s;
  animation-timing-function: ease-in-out;
}

.about .wrapper .images img:nth-child(1) {
  top: 5%;
  left: 5%;
}

.about .wrapper .images img:nth-child(2) {
  top: 20%;
  left: 40%;
}

.about .wrapper .images img:nth-child(3) {
  top: 60%;
  left: 15%;
}

.service {
  position: relative;
  background-image: url("../img/service_bg.jpg");
  background-repeat: no-repeat;
  background-position: 20%;
  background-size: cover;
  overflow: hidden;
  z-index: 0;
}

.service::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  border-top: 15vw solid #fff;
  border-right: 100vw solid transparent;
}

.service::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 15vw solid #fff;
  border-left: 100vw solid transparent;
}

.service-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(150deg, #05daff, #2f00ff);
  opacity: 0.6;
  z-index: -1;
}

.service .wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.service .wrapper .card {
  height: 400px;
  width: 300px;
  background: #dcf3f2;
  margin: 0 15px;
  padding: 50px 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.service .wrapper .card-icon {
  font-size: 55px;
}

.service .wrapper .card-title {
  font-size: 20px;
  color: #888;
  font-weight: bold;
}

.service .wrapper .card-text {
  color: #666;
}

.product .wrapper {
  margin-top: 100px;
  display: flex;
  justify-content: space-around;
}

.product .wrapper .cardbig {
  height: 500px;
  width: 350px;
  box-shadow: #888 0 0 10px 1px;
}

.product .wrapper .cardbig-img {
  position: relative;
}

.product .wrapper .cardbig-img img {
  position: relative;
  width: 100%;
}

.product .wrapper .cardbig-img img::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 15vw solid #fff;
  border-left: 100vw solid transparent;
  z-index: -1;
}

.product .wrapper .cardbig-img_title {
  position: absolute;
  display: inline-block;
  font-size: 20px;
  padding: 5px 10px;
  color: #fff;
  background: #6b4dee;
  opacity: 0.8;
  right: 5%;
  bottom: 7px;
}

.product .wrapper .cardbig ul {
  list-style: none;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 40px;
}

.product .wrapper .cardbig ul li {
  text-align: center;
  width: 70%;
  border-bottom: #e6e6e6 1px solid;
  padding-bottom: 5px;
  margin: 5px 0;
}

.product .wrapper .cardbig ul li:last-child {
  border: none;
}

.impression {
  position: relative;
  background-image: url("../img/impression_bg.jpg");
  background-repeat: no-repeat;
  background-position: 20%;
  background-size: cover;
  overflow: hidden;
  z-index: 0;
}

.impression-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  opacity: 0.5;
  z-index: -1;
}

.impression .wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.impression .wrapper .storys {
  height: 300px;
  width: 70%;
  margin: 20px 0;
  padding: 0 20px;
  background: #fff;
  display: flex;
  align-items: center;
  transform: skew(170deg);
  box-shadow: #888 0 0 15px 3px;
}

.impression .wrapper .storys-image {
  transform: skew(-170deg);
  height: 100%;
  width: 30%;
}

.impression .wrapper .storys-image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: -170px 0px;
  border-radius: 50%;
}

.impression .wrapper .storys-sentence {
  width: 70%;
  margin: 0 30px;
  transform: skew(-170deg);
}

.impression .wrapper .storys-sentence_title {
  font-size: 30px;
  color: #888;
}

.impression .wrapper .storys-sentence_text {
  font-size: 20px;
  color: #666;
}

footer {
  background: #313131;
  color: #fff;
  height: 20vh;
  line-height: 20vh;
  text-align: center;
}
